import React from 'react';
import {CrossIcon} from "../Icons";
import './map.less';
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';
import connect from "react-redux/es/connect/connect";
import {fetchAlbums, saveFotoInformation} from "../../actions/albumActions";

@connect(store => ({}), {
    saveFotoInformation
})
class GMap extends React.Component {

    constructor(props) {
        super(props);
        const foto = props.images[props.index];
        this.state = {
            selectedPlace: {},
            currentLocation: {},
            foto: foto
        }
    }

    componentDidMount() {
        const foto = this.props.images[this.props.index];
        if (foto.position) {
            this.setState({ currentLocation: foto.position });
        } else if (navigator && navigator.geolocation) {
            navigator.geolocation.getCurrentPosition((pos) => {
                const coords = pos.coords;
                this.setState({
                    currentLocation: {
                        lat: coords.latitude,
                        lng: coords.longitude
                    }
                })
            })
        }
    }

    onCrossClick() {
        this.props.close();
    }

    onMarkerClick(props, marker, e) {
        console.dir(props);
        console.dir(marker);
        console.dir(e);
    }

    onInfoWindowClose(g) {
        console.log(g);
    }

    onMapClicked(g, e, r) {
        console.log(r.latLng.lat());
        const foto = Object.assign({}, this.state.foto);
        foto.position = {lat: r.latLng.lat(), lng: r.latLng.lng()};
        this.setState({foto});
        this.props.changeCoordinates(foto);
        this.props.saveFotoInformation(foto);
    }

    render() {
        const {foto} = this.state;
        let images =[...this.props.images];
        images.splice(this.props.index, 1);

        if (!this.state.currentLocation || !this.state.currentLocation.lat) return null;
        if(!this.props.google) return null;

        return <div className="map_container">
            <CrossIcon className='cross'
                       onClick={() => this.onCrossClick()}/>
            <Map
                google={this.props.google}
                zoom={14}
                initialCenter={ this.state.currentLocation }
                onClick={(a, b, c) => this.onMapClicked(a, b, c)}
            >

                {foto.position &&
                <Marker onClick={(a,b,c) => this.onMarkerClick(a,b,c)}
                        key="active"
                        title={foto.name}
                        name={foto.name}
                        position={foto.position}
                        icon={{
                            url: "../../svg/placeholder.svg",
                            anchor: new google.maps.Point(16,16),
                            scaledSize: new google.maps.Size(32,32)
                        }}
                />}

                {images && images.map((image, index) => <Marker onClick={(a,b,c) => this.onMarkerClick(a,b,c)}
                                                       key={index}
                                                       title={image.name}
                                                       name={image.name}
                                                       position={image.position}
                />)}

                {/*<Marker*/}
                    {/*name={'Your position'}*/}
                    {/*position={{lat: 37.762391, lng: -122.439192}}*/}
                    {/*icon={{*/}
                        {/*url: "/path/to/custom_icon.png",*/}
                        {/*anchor: new google.maps.Point(32,32),*/}
                        {/*scaledSize: new google.maps.Size(64,64)*/}
                    {/*}} />*/}

                {/*<InfoWindow onClose={this.onInfoWindowClose}>*/}
                    {/*<div>*/}
                        {/*<h1>{this.state.selectedPlace.name}</h1>*/}
                    {/*</div>*/}
                {/*</InfoWindow>*/}
            </Map>
        </div>
    }
}

export default GoogleApiWrapper({
    apiKey: ("AIzaSyDWNe8nbXT808Y9VT3ei5JbVBR-jBK-Y0s")
})(GMap)


